import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import Home from './pages/Home.vue'
import Contact from './pages/Contact.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: Home },
  { path: '/contact', component: Contact }
]


const router = new VueRouter({
  routes: routes
})

new Vue({
  el: '#app',
  render: h => h(App),
  router: router
})



